import React, { PureComponent } from 'react';
import '@vkontakte/vkui/dist/vkui.css';
import {Avatar, Button, Cell} from "@vkontakte/vkui";
import {StickerImg} from "./StickerImg";

export class StickerButton extends PureComponent {

    render() {
        return (
            this.props.mode === 'cell' ?
                <Cell id={this.props.id} onClick={this.props.onClick} before={this.props.hasOwnProperty('src') && (this.props.hasOwnProperty('img_type') ? (this.props.img_type === 'round' && <Avatar size={78} src={this.props.src} />) : <StickerImg mode={'medium'} src={this.props.src}/>)}
                      description={this.props.description && <div dangerouslySetInnerHTML={{ __html: this.props.description.toString().replace('\n','<br/>') }} />} size={"l"}
                      bottomContent={this.props.hasOwnProperty('price') ? <Button mode="commerce">{this.props.price + ' руб.'}</Button> : this.props.hasOwnProperty('buttons') && this.props.buttons}
                      asideContent={this.props.hasOwnProperty('more') ?
                          <div style={{ alignSelf: "center" }}>
                              {this.props.more}
                          </div>
                          : null}
                >
                    {this.props.title}
                </Cell>
                :
                this.props.mode === 'img' &&
                <StickerImg
                    mode={"big"}
                    src={this.props.src}
                    id={this.props.id}
                    onClick={this.props.onClick}
                />
        )
    }

}